import React from 'react';
import { 
  BrowserRouter as Router, 
  Route, 
  Link, 
  Switch 
} from 'react-router-dom'; 
import Home from './component/Home';
import Cart from './component/Cart'
import './App.css';


class App extends React.Component{
  constructor(props){
    super(props);

    this.state = {"items":[{"name":"item 1","price":{"actual":319,"display":900},"discount":64},{"name":"item 2","price":{"actual":320,"display":900},"discount":64},{"name":"item 3","price":{"actual":319,"display":900},"discount":64},{"name":"item 4","price":{"actual":319,"display":900},"discount":64},{"name":"item 5","price":{"actual":319,"display":900},"discount":64},{"name":"item 6","price":{"actual":319,"display":900},"discount":64},{"name":"item 7","price":{"actual":319,"display":900},"discount":64},{"name":"item 8","price":{"actual":319,"display":900},"discount":64},{"name":"item 9","price":{"actual":319,"display":900},"discount":64},{"name":"item 10","price":{"actual":319,"display":900},"discount":64},{"name":"item 11","price":{"actual":319,"display":900},"discount":64},{"name":"item 12","price":{"actual":319,"display":900},"discount":64},{"name":"item 13","price":{"actual":319,"display":900},"discount":64},{"name":"item 14","price":{"actual":319,"display":900},"discount":64}],
                  "cart":0,
                  "CartValue":[]
                 };

    this.CbtnRef = React.createRef();

    this.UpdateStateCart = this.UpdateStateCart.bind(this);
    this.RemoveCartProp = this.RemoveCartProp.bind(this);
  }
  UpdateStateCart(cartedName, cartedPrice, event){
    const obj = {'cName':cartedName, 'cPrice':cartedPrice}
    this.setState({
      CartValue:[...this.state.CartValue, obj]
    });
    this.setState({cart: this.state.cart+1});
    //this.CbtnRef.current.setAttribute("disabled", "disabled");
    var dbutton = event.target.id;
    //console.log(event.target.id);
    document.getElementById(dbutton).style.display="none";
    //console.log(this.CbtnRef.current.id);
  }

  RemoveCartProp(CName){
    this.setState(prevState => {
      const CartValue = prevState.CartValue.filter(task => task.cName !== CName);
      return{CartValue};
  });
  this.setState({cart: this.state.cart-1});
  }
  
  render(){
    return(
      <div className="header">
      <Router>
            <ul className="Tmenu"> 
                <li> 
                  <Link to="/Home">
                  <span><i className="fa fa-home"></i></span>  
                  </Link> 
                </li> 
                <li>
                <span><i className="fa fa-search"></i></span>
                </li>
                <li> 
                    <Link to="/Cart">
                      <span><i className="fa fa-shopping-cart"></i></span>
                      <span><b>{this.state.cart}</b></span>
                    </Link> 
                </li> 
                
            </ul> 
      
            <Switch> 
              <Route exact path='/Cart' render={()=>(<Cart CartProp={this.state.CartValue} CartCount={this.state.cart} RemoveCartProp={this.RemoveCartProp}/>)} />
              <Route exact path='/Home' render={()=>(<Home DataProp={this.state.items} UpdateStateCart={this.UpdateStateCart} 
              CbtnRef={this.CbtnRef}/>)} />
            </Switch> 
            </Router>
            </div>
    )
  }
}

export default App;